import moment from "moment";
import React from "react";
import { useSelector } from "react-redux";
import styled from "styled-components";


export default function PaymentPending() {
    const { list } = useSelector((state) => state.orders);

    let listFilter = [];
    if (list.length > 0) {
        listFilter = list.filter((order) => order.payment_status === "A régler sur place");
    }

    return (
        <StyledTransactions>
            <h3>Paiements en attente</h3>
            {
                listFilter.length > 0 ?
                    listFilter.map((order, index) => (
                        <Transaction key={index}>
                            <p>{order.userFirstName + " " + order.userLastName}</p>
                            <p>{order.total.toLocaleString()} €</p>
                            <p>{moment(order.createdAt).fromNow()}</p>
                        </Transaction>
                    ))
                    : <p>Aucun paiement en attente</p>
            }
            {listFilter.length === 0 && list.length === 0 && <p>Aucune commande</p>}
        </StyledTransactions>
    );
}

const StyledTransactions = styled.div`
    background: rgb(48, 51, 78);
    color: rgba(234, 234, 255, 0.87);
    padding: 1rem;
    border-radius: 5px;
    margin-top: 1rem;
`;

const Transaction = styled.div`
    display: flex;
    font-size: 14px;
    margin-top: 1rem;
    padding: 0.5rem;
    border-radius: 3px;
    background: rgba(38, 198, 249, 0.12);
    p{
        flex: 1;
    }

    &:nth-child(even){
        background: rgba(102, 108, 255, 0.12)
    }
`;